.page {
  max-width: 600px;
  margin: 40px auto;
  padding: 30px;
  background: var(--card-color);
  border-radius: 18px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
  animation: fadeIn 0.4s ease;
}

.page h1 {
  font-size: 26px;
  margin-bottom: 25px;
  font-weight: 600;
  text-align: center;
  color: var(text-color);
}

.add-tech-form {
  display: flex;
  flex-direction: column;
  gap: 22px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-size: 15px;
  font-weight: 500;
  color: var(--text-color);
}

input,
textarea,
select {
    color: var(--text-color);
  font-size: 15px;
  padding: 12px 14px;
  border: 1px solid var(--bg-color);
  border-radius: 10px;
  background: var(--input-bg-color);
  transition: 0.25s;
}

input:focus,
textarea:focus,
select:focus {
    color: var(--text-color);
  background: var(--input-bg-color) ;
  border-color: #4a90e2;
  box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.15);
  outline: none;
}

textarea {
  resize: vertical;
}

.form-actions {
  display: flex;
  justify-content: space-between;
  gap: 14px;
  margin-top: 10px;
}