.search-div {
    justify-content: center;
    align-items: center;
    display: flex;
    gap: 10px
}

button {
  padding: 12px 16px;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s ease;
  font-weight: 500;
}

.search-div input {
    padding: 10px;
    border-radius: 6px;
    border: 1px solid #ccc;
    font-size: 14px;
}

.search-div input:focus {
  outline: none;
}