:root {
  --bg-color: #ffffff;
  --text-color: #000000;
  --card-color: white;
  --desc-color: #666
  --input-bg-color: #333;
}

[data-theme="dark"] {
  --bg-color: #19202a;
  --text-color: #e5e5e5;
  --card-color: #1f2937;
  --desc-color: #aaaaaa;
  --input-bg-color: #2d3748;
}

body {
  background: var(--bg-color);
  color: var(--text-color);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.div-btns {
  margin-bottom: 15px;
}

.quick-btn {
  border: none;
  border-radius: 10px;
  padding: 10px;
  color: white;
  background: #bababa;
  margin: 5px;
  transition: 0.5s;
  /* transform-origin: right bottom; */
}

.btn {
  width: 100%;
  padding: 12px 16px;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s ease;
  font-weight: 500;
}

.btn-pr {
  background: #10b981;
  color: white;
}

.btn-pr:hover {
  background: #12a474;
}

.btn-sec {
  background: #e5e5e5;
  color: #333;
}

.btn-sec:hover {
  background: #d5d5d5;
}

